import {useSelector} from "react-redux";
import {Grid, Paper, Typography} from "@mui/material";
import {useEffect, useState} from "react";
import {DivvyCryptoFormat, DivvyUSDFormat} from "../utils";

export default function DivvyHomePage() {

    const {loaded, projects, projectsMap, bnbPrice} = useSelector(state => state.divvy);
    const [usdValue, setUsdValue] = useState(0);
    const [bnbValue, setBnbValue] = useState(0);
    const [backingAmt, setBackingAmt] = useState(0);

    useEffect(() => {

        if(!loaded)return;

        let totalDollar = 0;
        let totalBNB = 0;
        let totalBacking = 0;


        projects.map(project => {
            let stats = projectsMap[project.contract_address].stats;

            let divvyValueUSD = stats.price * bnbPrice * stats.divvyBalance;
            let divvyValueBNB = stats.price * stats.divvyBalance;
            let divvyBacking = stats.backingPerToken * stats.divvyBalance;

            totalDollar += divvyValueUSD;
            totalBNB += divvyValueBNB;
            totalBacking += divvyBacking;

            // if(project.symbol === 'DIVVY')console.log(projectsMap[project.contract_address]);
            return project;
        })

        setUsdValue(totalDollar);
        setBnbValue(totalBNB);
        setBackingAmt(totalBacking);

    }, [projects, projectsMap, bnbPrice, loaded])


    return <Grid container spacing={2} style={{padding:20}}>
        <Grid item xs={6}>
            <Paper elevation={0}>
                <Grid container>
                    <Grid item>
                        <Typography variant="h4">{projects.length}</Typography>
                        <Typography variant="caption">Projects Listed</Typography>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
        <Grid item xs={6}>
            <Paper elevation={0}>
                <Grid container>
                    <Grid item>
                        <Typography variant="h4">{DivvyUSDFormat(bnbPrice)}</Typography>
                        <Typography variant="caption">BNB Price</Typography>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>

        <Grid item xs={12}>
            <Paper elevation={0}>
                <Grid container>
                    <Grid item>
                        <Typography variant="h4">{DivvyUSDFormat(usdValue)}</Typography>
                        <Typography variant="caption">Divvy Fund Total USD Value</Typography>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>

        <Grid item xs={12}>
        <Paper elevation={0}>
            <Grid container>
                <Grid item>
                    <Typography variant="h4">{DivvyCryptoFormat(bnbValue)}</Typography>
                    <Typography variant="caption">Divvy Fund Total BNB Value</Typography>
                </Grid>
            </Grid>
        </Paper>
        </Grid>

        <Grid item xs={12}>

        <Paper elevation={0}>
            <Grid container>
                <Grid item>
                    <Typography variant="h4">{DivvyCryptoFormat(backingAmt)}</Typography>
                    <Typography variant="caption">Divvy Fund Total BNB Asset Backing</Typography>
                </Grid>
            </Grid>
        </Paper>
        </Grid>
    </Grid>
}