import {
    Avatar, Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText, Paper,
    styled, Typography
} from "@mui/material";
import {
    Link as RouterLink
} from "react-router-dom";
import LockIcon from '@mui/icons-material/Lock';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import PendingIcon from "@mui/icons-material/Pending";
import {DivvyCryptoFormat, DivvyUSDFormat} from "../utils";
import millify from "millify";
import {useSelector} from "react-redux";

const DivvyAvatar = styled(Avatar)({
    backgroundColor: 'transparent'
})

const millifyConfig = {
    space: true,
    precision: 2,
    units: ['', 'K', 'M', 'B', 'T']
}

export default function MyProjectList() {

    const {projects, projectsMap, bnbPrice, wallet} = useSelector(state => state.divvy);

    const list = [...projects];

    list.sort((a, b) => {

        if (!wallet.balances[a.contract_address] || !wallet.balances[b.contract_address]) {
            return null;
        }
        const a2 = projectsMap[a.contract_address];
        const b2 = projectsMap[b.contract_address];

        const a3 = wallet.balances[a.contract_address];
        const b3 = wallet.balances[b.contract_address];

        if (!a2.stats || !b2.stats) {
            return null;
        }

        if ((a3.balance * a2.stats.price) < (b3.balance * b2.stats.price)) {
            return 1;
        }
        if ((a3.balance * a2.stats.price) > (b3.balance * b2.stats.price)) {
            return -1;
        }
        return 0;
    });

if(!wallet.address) {
    return <Paper elevation={0}>
        <Grid container>
            <Grid item xs={12}>
                <Typography textAlign="center">Connect Wallet to view <strong>My Projects</strong></Typography>
            </Grid>
        </Grid>
    </Paper>
}

    return (
        <List style={{marginBottom: 48}}>
            {
                list.map(project => {

                    const stats = projectsMap[project.contract_address]?.stats;
                    const balance =  wallet.balances[project.contract_address]?.balance;
                    const {price} = stats;

                    if (!stats || balance === 0 || (balance * price) < 0.01) return null;


                    const pending = (stats.divvyBalance < (stats.supply * .01)) && !(!!(project.flags & 2));

                    return <RouterLink
                        key={project.symbol}
                        to={`/${project.symbol}`}
                        style={{textDecoration: 'none', color: 'inherit'}}
                    >
                        <ListItem>
                            <ListItemAvatar>
                                <DivvyAvatar>
                                    {
                                        pending ?
                                            <PendingIcon color="primary"/>
                                            :
                                            <img
                                                alt={`${project.name} Logo`}
                                                src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract_address}/logo.png`}
                                                width={40}/>
                                    }
                                </DivvyAvatar>
                            </ListItemAvatar>
                            <ListItemText primary={project.name} secondary={
                                <>
                                    {project.symbol}
                                    {project.locker_address !== '0x0000000000000000000000000000000000000000' &&
                                        <LockIcon sx={{fontSize: 15, marginLeft: 1}}/>}
                                    {project.faucet_address !== '0x0000000000000000000000000000000000000000' &&
                                        <LocalAtmIcon sx={{fontSize: 15}}/>}
                                </>
                            }/>
                            <ListItemText
                                primary={`${DivvyUSDFormat((balance * price * bnbPrice) || 0)}`}
                                primaryTypographyProps={{textAlign: 'right'}}
                                secondary={`${DivvyUSDFormat((price * bnbPrice) || 0)}`}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                    </RouterLink>
                })
            }
        </List>
    )
}