import {makeBatchRequest} from "./promisify";
import sd_abi from '../abis/sd_abi.json';

import store from "../redux/store";
import {updatePersonalBalance} from "../redux/slices/divvySlice";
import {dataseed} from "./index";

const getPersonalStats = (contract_address, address) => {

    const c = new dataseed.eth.Contract(sd_abi, contract_address);

    const calls = [
        c.methods.balanceOf(address).call
    ];

    // don't check for ROX
    if (contract_address !== '0xa3D522c151aD654b36BDFe7a69D0c405193A22F9') {
        calls.push(c.methods.lendData(address).call)
        calls.push(c.methods._loansBase(address).call)
        calls.push(c.methods._loansToken(address).call)
        calls.push(c.methods.amountToRepay(address).call)
        calls.push(c.methods.collateral().call)
        calls.push(c.methods.userBalanceInternal(address).call)
    }

    makeBatchRequest(dataseed, calls)
        .then(resp => {
            let [
                balance,
                lendData,
                _loansBase,
                _loansToken,
                amountToRepay,
                collateral,
                userBalanceInternal
            ] = resp;
            balance = balance / Math.pow(10, 9) || 0;

            if (contract_address !== '0xa3D522c151aD654b36BDFe7a69D0c405193A22F9') {
                _loansBase = _loansBase / Math.pow(10, 18) || 0;
                _loansToken = _loansToken / Math.pow(10, 9) || 0;
                amountToRepay = amountToRepay / Math.pow(10, 18) || 0;
                collateral = collateral / Math.pow(10, 9) || 0;
                userBalanceInternal = {
                    main: userBalanceInternal.main / Math.pow(10, 18) || 0,
                    token: userBalanceInternal.token / Math.pow(10, 9) || 0
                }
            }

            store.dispatch(updatePersonalBalance({
                contract_address,
                balance,
                lendData,
                _loansBase,
                _loansToken,
                amountToRepay,
                collateral,
                userBalanceInternal
            }));
        })
        .catch(err => {
            console.warn(contract_address, address, err)
        });
}

export default getPersonalStats;