import {useDispatch, useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {
    BottomNavigation,
    BottomNavigationAction,
    Paper,
} from "@mui/material";
import DivvyLoading from "../components/DivvyLoading";
import BarChartIcon from '@mui/icons-material/BarChart';
import ListIcon from '@mui/icons-material/List';
import InfoIcon from '@mui/icons-material/Info';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import ProjectList from "../components/ProjectList";
import DivvyHomePage from "../components/DivvyHomePage";
import Menu from "../components/Menu";
import MyProjectList from "../components/MyProjectList";
import {updateNavItem} from "../redux/slices/divvySlice";

export default function HomePage() {

    const {loaded, navItem} = useSelector(state => state.divvy);
    const dispatch = useDispatch();
    const [open, setOpen] = useState(true);

    useEffect(() => {
        if (loaded) {
            setOpen(false)
        }
    }, [loaded])

    useEffect(()=>{
        window.document.title = `Divvy Dashboard`
    }, [])

    return <>
        <DivvyLoading open={open} label="Preparing Awesome Sauce..."/>


        {
            navItem === 0 && <ProjectList />
        }
        {
            navItem === 1 && <MyProjectList />
        }
        {
            navItem === 2 && <DivvyHomePage />
        }
        {
            navItem === 3 && <Menu />
        }

        <Paper sx={{position: 'fixed', bottom: 0, left: 0, right: 0}} elevation={3}>
            <BottomNavigation
                showLabels
                value={navItem}
                onChange={(event, newValue) => {
                    dispatch(updateNavItem(newValue));
                }}
            >
                <BottomNavigationAction label="Projects" icon={<ListIcon/>}/>
                <BottomNavigationAction label="My Projects" icon={<AccountBalanceWalletIcon/>}/>
                <BottomNavigationAction label="Stats" icon={<BarChartIcon/>}/>
                <BottomNavigationAction label="Info" icon={<InfoIcon/>}/>
            </BottomNavigation>
        </Paper>
    </>
}