import {useEffect, useState} from "react";
import {
    AppBar,
    Button,
    createTheme,
    CssBaseline,
    Grid,
    ThemeProvider,
    Toolbar,
} from "@mui/material";
import {
    HashRouter as Router,
    Routes,
    Route,
    Link as RouterLink
} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import ProjectDetails from "./views/ProjectDetails";
import {
    resetWallet,
    updateBNBPrice,
    updateProjects,
    updateWalletAddress,
    updateWalletChain,
    updateWalletProviderType
} from "./redux/slices/divvySlice";
import {useGetTickersByTypeQuery} from "./redux/services/BinanceTickerApi";
import Logo from './images/logo_small.png';
import getProjectStats from "./utils/projectInfo";
import Web3 from "web3";
import useWeb3Modal from "./hooks/useWeb3Modal";
import getPersonalStats from "./utils/personalInfo";

import divvy_listings_abi from "./abis/DivvyListings.json";
import {dataseed} from './utils';
import Admin from "./views/Admin";
import NewListing from "./views/NewListings";
import HomePage from "./views/HomePage";

const theme = createTheme({
    typography: {
        fontFamily: `'Lato', sans-serif`
    },
    palette: {
        background: {
            default: "#fafafa"
        },
        primary: {
            main: '#872eff'
        }
    },
    components: {
        MuiPaper: {
            styleOverrides: {
                elevation0: {
                    padding: 20,
                    border: '1px solid #eee',
                    height: '100%'
                }
            }
        }
    }
});

function DivvyDashboard() {

    const dispatch = useDispatch();
    const {bnbPrice, projects, wallet} = useSelector(state => state.divvy);

    const [web3Modal, setWeb3Modal] = useState(null);
    const web3ModalProvider = useWeb3Modal();

    // get BNB price
    let binance = useGetTickersByTypeQuery('price?symbol=BNBUSDT', {
        pollingInterval: 30000,
    });

    useEffect(() => {
        if (binance.status === 'fulfilled') {
            dispatch(updateBNBPrice({price: binance.data.price}));
        }
    }, [binance.status, binance.data, dispatch]);

    //get projects list
    useEffect(() => {
        const c = new dataseed.eth.Contract(divvy_listings_abi, '0x5cf76803e834B0D16172ECeD1175F55c4881eecA');
        c.methods.getListings().call((err, resp) => {
            if (err) {
                return
            }

            const list = [];
            resp.map(project => {

                if (!(project.flags & 1)) {
                    return project;
                }

                const {
                    creator,
                    contract_address,
                    fund_address,
                    faucet_address,
                    locker_address,
                    name,
                    symbol,
                    color,
                    floor,
                    flags
                } = project;

                list.push({
                    creator,
                    contract_address,
                    fund_address,
                    faucet_address,
                    locker_address,
                    name,
                    symbol,
                    color,
                    floor: parseInt(floor),
                    flags: parseInt(flags)
                });

                return project;
            });

            dispatch(updateProjects(list));

            /**
             * bypassMin: true
             */

        })
    }, [dispatch]);

    //get project stats
    useEffect(() => {

        if (!projects.length) return;

        const makeRequest = () => {
            projects.map(project => {
                getProjectStats(project);
                return project;
            })
        }

        makeRequest();
        const interval = setInterval(makeRequest, 30000);
        return () => clearInterval(interval);
    }, [projects]);

    //TODO do this for each project, not overall
    //get projects list
    useEffect(() => {

        const makeRequest = () => {
            if (wallet.address) {
                projects.map(project => {
                    getPersonalStats(project.contract_address, wallet.address);
                    return project;
                })
            }
        }

        makeRequest();

        const interval = setInterval(makeRequest, 60000);
        return () => clearInterval(interval);
    }, [dispatch, wallet.address, projects]);

    useEffect(() => {
        changeWeb3Modal();
    }, [])

    async function changeWeb3Modal() {
        const web3mod = await web3ModalProvider.getWeb3Modal();
        if (web3mod.providerController.cachedProvider) {
            const provider = await web3mod.connect()
            await connectProvider(provider)
        }
        setWeb3Modal(web3mod);
    }

    async function connectWallet() {
        const provider = await web3Modal.connect();
        await connectProvider(provider)
    }

    const connectProvider = async (provider) => {
        window.provider = provider
        const web3 = new Web3(provider);
        subscribeProvider(provider)
        const accounts = await web3.eth.getAccounts();
        if (accounts && accounts.length) {
            //set wallet address
            dispatch(updateWalletAddress(accounts[0]))
        }
        const chainId = parseInt(await web3.eth.net.getId())
        dispatch(updateWalletChain(chainId));
        dispatch(updateWalletProviderType(!!provider.bridge ? 'walletconnect' : 'injected'))
    }

    const subscribeProvider = (provider) => {
        provider.on("close", () => {
            clearWallet();
        });

        provider.on("accountsChanged", async (acc) => {
            dispatch(resetWallet())
            if (window.provider) {
                dispatch(updateWalletAddress(acc[0]))
            }
        });

        provider.on("chainChanged", async (networkId) => {
            dispatch(resetWallet())
            if (window.provider) {
                const chainId = parseInt(networkId)
                dispatch(updateWalletChain(chainId));
            }
        });
    }

    const clearWallet = async () => {
        const web3 = new Web3(window.provider);
        if (web3 && web3.currentProvider && web3.currentProvider.close) {
            await web3.currentProvider.close();
        }
        dispatch(resetWallet())
        await web3Modal.clearCachedProvider();
        window.provider = null;
    }

    if (!projects.length || bnbPrice === 0) return <></>;

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline/>
            <Router>
                <AppBar color="primary">
                    <Toolbar>
                        <RouterLink
                            to={`/`}
                            style={{textDecoration: 'none'}}
                        >
                            <img alt="Divvy" src={Logo} height={32} style={{marginTop: 5}}/>
                        </RouterLink>
                        <div style={{flexGrow: 1}}/>
                        {
                            wallet.address ?
                                <Button
                                    variant="outlined"
                                    color="inherit"
                                    size="small"
                                    onClick={() => {dispatch(resetWallet()); clearWallet();}}
                                >0X... {wallet.address?.substr(-6)}</Button>
                                :
                                <Button
                                    variant="outlined"
                                    color="inherit"
                                    size="small"
                                    onClick={() => connectWallet()}>Connect</Button>
                        }
                    </Toolbar>
                </AppBar>
                <Grid container style={{marginTop: 64}}>
                    <Grid item xs={12}>
                        <Routes>
                            <Route
                                path={`/`}
                                exact={true}
                                element={<HomePage/>}
                            />
                            <Route
                                path={`/admin`}
                                exact={true}
                                element={<Admin/>}
                            />
                            <Route
                                path={`/list`}
                                exact={true}
                                element={<NewListing/>}
                            />
                            {
                                projects.map((project, i) => {
                                    return <Route
                                        key={i}
                                        path={`/${project.symbol}`}
                                        exact={true}
                                        element={<ProjectDetails project={project}/>}
                                    />
                                })
                            }
                        </Routes>
                    </Grid>
                </Grid>
            </Router>
        </ThemeProvider>
    );
}

export default DivvyDashboard;