import {Avatar, Grid, List, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {useSelector} from "react-redux";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import {BNBIcon} from "../utils";
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import CreditScoreIcon from '@mui/icons-material/CreditScore';
import EventIcon from '@mui/icons-material/Event';
import StatDetail from "./StatDetail";
import {useEffect, useState} from "react";

export default function PersonalStats(props) {

    const {project} = props;
    const {bnbPrice, wallet, projectsMap} = useSelector(state => state.divvy);
    const {stats} = projectsMap[project.contract_address];

    const balance = wallet.balances[project.contract_address]?.balance;
    const lendData = wallet.balances[project.contract_address]?.lendData;
    const _loansBase = wallet.balances[project.contract_address]?._loansBase;
    const _loansToken = wallet.balances[project.contract_address]?._loansToken;
    const amountToRepay = wallet.balances[project.contract_address]?.amountToRepay;
    const collateral = wallet.balances[project.contract_address]?.collateral;
    const userBalanceInternal = wallet.balances[project.contract_address]?.userBalanceInternal;

    // sponsor check
    const [viewable, setViewable] = useState(false);
    const [tokensNeeded, setTokensNeeded] = useState(0);
    const sponsorToken = '0x820671E3647d8aaa22Feab05aaA14AB1665e06e1';
    const sponsorMin = 10;// in USD
    useEffect(() => {
        setViewable(false);
        setTokensNeeded(sponsorMin / bnbPrice / projectsMap[sponsorToken]?.stats?.price)
        if (!wallet.balances[sponsorToken]?.balance) return;
        if (wallet.balances[sponsorToken].balance && projectsMap[sponsorToken]?.stats?.price && bnbPrice) {

            const userVal = wallet.balances[sponsorToken].balance * projectsMap[sponsorToken]?.stats?.price * bnbPrice;

            if (userVal >= sponsorMin) {
                setViewable(true);
            } else {
                setViewable(false);
            }
        }
    }, [wallet.balances, projectsMap, bnbPrice]);


    let dateLabel = "";

    if (lendData) {
        dateLabel = new Date((lendData * 1000)).toLocaleDateString('en-us', {
            year: "numeric",
            month: "short",
            day: "numeric"
        })
    }

    return <Paper elevation={0}>
        {
            viewable ? <List disablePadding dense
                             subheader={<Typography>My Wallet <Typography variant="caption" style={{float: 'right'}}>sponsored
                                 by
                                 FOOK</Typography></Typography>}>

                    <StatDetail
                        avatar={<AccountBalanceWalletIcon sx={{color: project.color}}/>}
                        primary={`${DivvyCryptoFormat(balance)} ${project.symbol}`}
                        secondary="Balance"
                    />
                    <StatDetail
                        avatar={<AttachMoneyIcon sx={{color: project.color}}/>}
                        primary={`${DivvyUSDFormat(balance * stats.price * bnbPrice)}`}
                        secondary="Value USD"
                    />
                    <StatDetail
                        avatar={<BNBIcon color={project.color}/>}
                        primary={`${DivvyCryptoFormat(balance * stats.price)} BNB`}
                        secondary="Value BNB"
                    />
                    {
                        userBalanceInternal && userBalanceInternal.token !== 0 && <>
                            <Typography>SmartSwap</Typography>
                            <StatDetail
                                avatar={<AccountBalanceWalletIcon sx={{color: project.color}}/>}
                                primary={`${DivvyCryptoFormat(userBalanceInternal.token)} ${project.symbol}`}
                                secondary="Balance"
                            />
                            <StatDetail
                                avatar={<AttachMoneyIcon sx={{color: project.color}}/>}
                                primary={`${DivvyUSDFormat(userBalanceInternal.token * stats.price * bnbPrice)}`}
                                secondary="Value USD"
                            />
                            <StatDetail
                                avatar={<BNBIcon color={project.color}/>}
                                primary={`${DivvyCryptoFormat(userBalanceInternal.token * stats.price)} BNB`}
                                secondary="Value BNB"
                            />
                        </>
                    }

                    {
                        _loansToken !== undefined && _loansToken !== 0 && <>
                            <Typography>SmartLoan</Typography>
                            <StatDetail
                                avatar={<BNBIcon color={project.color}/>}
                                primary={`${DivvyCryptoFormat(_loansToken)} ${project.symbol}`}
                                secondary="Collateral"
                            />
                            <StatDetail
                                avatar={<CreditScoreIcon sx={{color: project.color}}/>}
                                primary={`${DivvyCryptoFormat(amountToRepay)} BNB`}
                                secondary="Repay Amount"
                            />
                            <StatDetail
                                avatar={<EventIcon sx={{color: project.color}}/>}
                                primary={dateLabel}
                                secondary="End Date"
                            />
                        </>
                    }

                </List>
                :
                wallet.address ?
                    <Grid container>
                        <Grid item xs={12}>
                            <Typography textAlign="center">To access My Wallet, you need to
                                hold <br/><strong>{DivvyUSDFormat(sponsorMin)}</strong> worth of FOOK.<br/><Typography
                                    textAlign="center"
                                    variant="caption">{DivvyCryptoFormat(tokensNeeded)} FOOK</Typography></Typography>


                        </Grid>
                    </Grid>
                    :
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar
                                src="https://fegexchange.github.io/assets/blockchains/smartchain/assets/0x820671E3647d8aaa22Feab05aaA14AB1665e06e1/logo.png"/>
                        </ListItemAvatar>
                        <ListItemText primary={<Typography>Connect wallet for <strong>My Wallet</strong></Typography>}
                                      secondary="Sponsored by FOOK"/>
                    </ListItem>
        }
    </Paper>
}