import {SpeedDial, SpeedDialAction, SpeedDialIcon} from "@mui/material";

import LanguageIcon from '@mui/icons-material/Language';
import TwitterIcon from "@mui/icons-material/Twitter";
import TelegramIcon from "@mui/icons-material/Telegram";
import ShowChartIcon from "@mui/icons-material/ShowChart";
import FindInPageIcon from '@mui/icons-material/FindInPage';
import SwapHorizIcon from '@mui/icons-material/SwapHoriz';
import LinkIcon from '@mui/icons-material/Link';


export default function ProjectLinks(props) {

    const {project, infoJSON} = props;

    return <SpeedDial
        ariaLabel="Links"
        sx={{position: 'fixed', bottom: 74, right: 16}}
        icon={<SpeedDialIcon icon={<LinkIcon/>}/>}
    >
        <SpeedDialAction
            icon={<ShowChartIcon/>}
            tooltipTitle="FEGcharts"
            FabProps={{
                href: `https://charts.fegex.com/charts/${project.contract_address}`,
                target: "_blank"
            }}
        />
        <SpeedDialAction
            icon={<SwapHorizIcon/>}
            tooltipTitle="Trade on FEGex"
            FabProps={{
                href: `https://fegex.com/trade/bsc/${project.contract_address}`,
                target: "_blank"
            }}
        />
        {
            infoJSON.website &&
            <SpeedDialAction
                icon={<LanguageIcon/>}
                tooltipTitle="Website"
                FabProps={{
                    href: infoJSON.website,
                    target: "_blank"
                }}
            />
        }
        {
            infoJSON.social && infoJSON.social.twitter &&
            <SpeedDialAction
                icon={<TwitterIcon/>}
                tooltipTitle="Twitter"
                FabProps={{
                    href: infoJSON.social.twitter,
                    target: "_blank"
                }}
            />
        }
        {
            infoJSON.social && infoJSON.social.telegram &&
            <SpeedDialAction
                icon={<TelegramIcon/>}
                tooltipTitle="Telegram"
                FabProps={{
                    href: infoJSON.social.telegram,
                    target: "_blank"
                }}
            />
        }
        {
            infoJSON.explorer &&
            <SpeedDialAction
                icon={<FindInPageIcon/>}
                tooltipTitle="BSCscan"
                FabProps={{
                    href: `https://bscscan.com/token/${project.contract_address}`,
                    target: "_blank"
                }}
            />
        }
    </SpeedDial>
}